const uni = require('uni').default;




















var app= require("../../common/common.js"); 
var id;
export default{
	data:{
		pageLoad:false, 
		pageData:{}
	},
	onLoad:function(option){
		id=option.id;
		this.getPage();
	},
	 
	methods:{
		getPage:function(){
			var that=this;
			uni.request({
				url:app.apiHost+"?m=forum&ajax=1&a=show&id="+id,
				success:function(data){
					that.pageLoad=true;
					that.pageData=data.data.data;
					 
				}
			})
		} 
	},
}



